<template>
  <div class="operate-task-log">
    <div class="topTitle">
      <span class="topTitle-span1">客户管理</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">产品客户</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">缺项管理</span>
      <div class="button-content">
        <Button type="primary"  @click="exportFn">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="客户号：">
                <Input v-model="formValidate.val1" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="客户名称：">
                <Input v-model="formValidate.val2" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="所属业务条线：">
                <Select v-model="formValidate.val3" style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in CD0031"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('0')">查询</Button>
                <Button type="primary" @click="handleSubmit('1')">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                   <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>{{item.csnm}}</td>
                  <!-- 客户号 -->
                  <td>{{item.ctnm}}</td>
                  <!-- 客户名称 -->
                  <td>{{item.address}}</td>
                  <!-- 住所 -->
                  <td>{{item.scopes}}</td>
                  <!-- 经营范围 -->
                  <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.citp"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.citp}}</p>
                  </el-popover>
                  </td>
                  <!-- 证件类型 -->
                  <td>{{item.ctid}}</td>
                  <!-- 证件号码 -->
                  <td>{{item.civt}}</td>
                  <!-- 证件有效期 -->
                  <td>{{item.scnm}}</td>
                  <!-- 控股股东或实际控制人姓名 -->
                  <td>{{item.scit}}</td>
                  <!-- 控股股东或实际控制人证件类型 -->
                  <td>{{item.scid}}</td>
                  <!-- 控股股东或实际控制人证件号 -->
                  <td>{{item.scvt}}</td>
                  <!-- 控股股东或实际控制人证件有效期 -->
                  <td>{{item.crnm}}</td>
                  <!-- 法定代表人姓名 -->
                  <td>{{item.crit}}</td>
                  <!-- 法定代表人证件类型 -->
                  <td>{{item.crid}}</td>
                  <!-- 法定代表人证件号 -->
                  <td>{{item.crvt}}</td>
                  <!-- 法定代表人证件有效期 -->
                  <td>{{item.headCtnm}}</td>
                  <!-- 负责人姓名 -->
                  <td>{{item.head_citp}}</td>
                  <!-- 负责人证件类型 -->
                  <td>{{item.head_ctid}}</td>
                  <!-- 负责人证件号 -->
                  <td>{{item.head_edt}}</td>
                  <!-- 负责人证件有效期 -->
                  <!-- ======= -->
                  <td>{{item.authorizedCtnm}}</td>
                  <!-- 受益所有人姓名 -->
                  <td>{{item.authorizedCitp}}</td>
                  <!-- 受益所有人证件类型 -->
                  <td>{{item.authorizedCtid}}</td>
                  <!-- 受益所有人证件号 -->
                  <td>{{item.authorizedEdt}}</td>
                  <!-- 益所有人证件有效期 -->
                  <!-- ======= -->
                  <td>{{item.beneficialCtnm}}</td>
                  <!-- 受益所有人姓名 -->
                  <td>{{item.beneficialCitp}}</td>
                  <!-- 受益所有人证件类型 -->
                  <td>{{item.beneficialCtid}}</td>
                  <!-- 受益所有人证件号 -->
                  <td>{{item.beneficialEdt}}</td>
                  <!-- 益所有人证件有效期 -->
                  <td>{{item.beneficialAddr}}</td>
                  <!-- 收益所有人地址 -->
                  <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.mbrc"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.mbrc}}</p>
                  </el-popover>
                  </td>
                  <!-- 所属业务条线 -->
                  <td>{{item.createSystem}}</td>
                  <!-- 创建系统名称 -->
                  <td>{{item.updateSystem}}</td>
                  <!-- 创更新系统名称 -->
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page
            :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"
          ></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    this.axios
      .get("/system/getDic/CD0031")
      .then(response => {
        if (response.code == "200") {
          that.CD0031 = response.data.CD0031;
        }
      })
      .catch(error => {});
    this.tableList();
  },
  data() {
    return {
      searchFlag: "0",
      pageSize: 10,
      total: 0,
      pageNum: 1,
      CD0031: [],
      tableHeader: [
         { name: "序" },
        { name: "客户号" },
        { name: "客户名称" },
        { name: "住所" },
        { name: "经营范围" },
        { name: "证件类型" },
        { name: "证件号码" },
        { name: "证件有效期" },
        { name: "控股股东或者实际控制人名称" },
        { name: "控股股东或者实际控制人身份证件种类" },
        { name: "控股股东或者实际控制人身份证件号码" },
        { name: "控股股东或者实际控制人身份证件有效期" },
        { name: "法定代表人名称" },
        { name: "法定代表人身份证件种类" },
        { name: "法定代表人证件号码" },
        { name: "法定代表人证件有效期" },
        { name: "负责人姓名" },
        { name: "负责人身份证件种类" },
        { name: "负责人证件号码" },
        { name: "负责人证件有效期" },
        { name: "授权办理业务人员名称" },
        { name: "授权办理业务人员身份证件种类" },
        { name: "授权办理业务人员身份证件号码" },
        { name: "授权办理业务人员证件有效期" },
        { name: "受益所有人名称" },
        { name: "受益所有人身份证件或者身份证明文件的种类" },
        { name: "受益所有人证件号码" },
        { name: "受益所有人有效期" },
        { name: "受益所有人地址" },
        { name: "所属业务条线" },
        { name: "创建系统（渠道）名称" },
        { name: "更新统（渠道）名称" }
      ],

      tableBody: [],
      formValidate: {
        val1: "",
        val2: "",
        val3: ""
      },
      ruleValidate: {
        val1: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val2: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          }
        ],
        val3: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val4: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
       download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
      let that = this;
      let data = {
       cstp: "03",
        flag: this.searchFlag,
        csnm: this.formValidate.val1,
        ctnm: this.formValidate.val2,
        mbrc: this.formValidate.val3
      };
      data = JSON.stringify(data);
      this.axios
        .post("/client/stanEmptyWarn/doExportEmptyWarnInfo", data, {
         headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
    handleSubmit(a) {
      this.searchFlag = a;
      this.pageNum = 1;
      this.tableList();
    },
    pageChangeFun(a) {
      this.pageNum = a * 1;
      this.tableList();
    },
    pageSizeChange(a) {
      this.pageSize = a * 1;
      this.tableList();
    },
    tableList() {
      let that = this;
      let data = {
        cstp: "03",
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        flag: this.searchFlag,
        csnm: this.formValidate.val1,
        ctnm: this.formValidate.val2,
        mbrc: this.formValidate.val3
      };
      data = JSON.stringify(data);
      this.axios
        .post("/client/stanEmptyWarn/getEmptyWarnInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.tableBody = response.data.records;
            this.total = response.data.total;
          }
        })
        .catch(error => {});
    }
  }
};
</script>
<style scoped>
.tableBox tr > td:nth-child(2) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(3) {
  min-width: 160px;
}
</style>



